<template>
    <v-container class="fill-height" fluid>
        <v-row class="align-center justify-center">
            <v-col cols="12" sm="8" md="6" lg="4">
                <v-alert
                    v-model="alert"
                    border="left"
                    close-text="Close Alert"
                    :type="alertType"
                    transition="scale-transition"
                    prominent
                >
                    {{ alertMessage }}
                </v-alert>

                <div class="text-center">
                    <v-btn
                        :color="getAppColors.primary"
                        class="white--text mx-auto mt-3"
                        rounded
                        large
                        v-if="passwordResetSuccess"
                        to="/"
                    >
                        Go to Login
                    </v-btn>
                </div>

                <v-fade-transition v-if="!passwordResetSuccess">
                    <v-card v-show="!loading" class="elevation-12 rounded-lg">
                        <v-toolbar :color="getAppColors.primary" dark>
                            <v-avatar size="50px">
                                <v-img
                                    alt="Logo"
                                    src="@/assets/logo-only.png"
                                    contain
                                />
                            </v-avatar>
                            <v-toolbar-title dark>
                                core<strong>CLEAR</strong> Portal
                            </v-toolbar-title>
                        </v-toolbar>
                        <v-card-title>Reset Password</v-card-title>
                        <v-card-text>
                            <v-form ref="form" v-model="valid">
                                <v-text-field
                                    prepend-icon="lock"
                                    :color="getAppColors.primary"
                                    :append-icon="
                                        showPassword ? 'mdi-eye' : 'mdi-eye-off'
                                    "
                                    label="Password"
                                    name="password"
                                    :type="showPassword ? 'text' : 'password'"
                                    autocomplete="current-password"
                                    v-model="password"
                                    :rules="passwordRules"
                                    @click:append="showPassword = !showPassword"
                                    required
                                />
                                <v-text-field
                                    prepend-icon="lock"
                                    :color="getAppColors.primary"
                                    :append-icon="
                                        showConfirmPassword
                                            ? 'mdi-eye'
                                            : 'mdi-eye-off'
                                    "
                                    label="Confirm Password"
                                    name="confirmPassword"
                                    :type="
                                        showConfirmPassword
                                            ? 'text'
                                            : 'password'
                                    "
                                    autocomplete="current-password"
                                    v-model="confirmPassword"
                                    :rules="confirmPasswordRules"
                                    @keyup.enter="submit"
                                    @click:append="
                                        showConfirmPassword =
                                            !showConfirmPassword
                                    "
                                    required
                                />
                            </v-form>
                        </v-card-text>
                        <v-card-actions>
                            <v-btn text rounded large to="/">Cancel</v-btn>
                            <v-spacer />
                            <v-btn
                                :color="getAppColors.primary"
                                class="white--text"
                                :disabled="!valid"
                                :loading="loading"
                                @click="submit"
                                rounded
                                large
                            >
                                Reset password
                            </v-btn>
                        </v-card-actions>
                    </v-card>
                </v-fade-transition>
                <v-dialog
                    v-model="loading"
                    hide-overlay
                    persistent
                    transition="dialog-bottom-transition"
                    width="300"
                >
                    <v-card :color="getAppColors.primary" dark>
                        <v-card-text>
                            Please wait...
                            <v-progress-linear
                                indeterminate
                                color="white"
                                class="mb-0"
                            ></v-progress-linear>
                        </v-card-text>
                    </v-card>
                </v-dialog>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import { mapGetters } from 'vuex';
import axios from 'axios';

export default {
    name: 'ResetPassword',
    data() {
        return {
            loading: false,
            valid: false,
            showPassword: false,
            showConfirmPassword: false,
            alert: false,
            alertMessage: '',
            alertType: 'success',
            password: '',
            confirmPassword: '',
            passwordRules: [
                (v) => !!v || 'Password is required',
                (v) =>
                    v.length >= 8 || 'Password must be at least 8 characters',
            ],
            confirmPasswordRules: [
                (v) => !!v || 'Password is required',
                (v) =>
                    v.length >= 8 || 'Password must be at least 8 characters',
                (v) => this.password === v || 'Password must match',
            ],
            passwordResetSuccess: false,
        };
    },
    computed: {
        ...mapGetters(['getAppColors', 'getInternalLoginController']),
    },
    methods: {
        openAlert(type, message) {
            this.alertType = type;
            this.alertMessage = message;
            this.alert = true;
        },
        submit() {
            if (this.$refs.form.validate()) {
                this.loading = true;
                this.alert = false;
                this.doResetPassword();
            }
        },
        async doResetPassword() {
            let formData = new FormData();
            formData.append('password', this.password);
            formData.append('confirm_password', this.confirmPassword);

            try {
                let response = await axios.post(
                    `${
                        this.getInternalLoginController +
                        '/do_reset_password/' +
                        this.$route.params.reset_key +
                        '/1'
                    }`,
                    formData,
                    {
                        withCredentials: true,
                        crossDomain: true,
                    }
                );

                this.loading = false;

                if (response.data.success === true) {
                    this.passwordResetSuccess = true;
                    this.openAlert('success', response.data.message);
                } else {
                    this.openAlert('error', response.data.message);
                }
            } catch (error) {
                this.openAlert(error.message);
                console.error(error);
            }
        },
    },
};
</script>
